.Deal{
    flex: 5;
    display: flex;
    flex-direction: column;
    background-color: $dealPageContainerBg;
    overflow: auto;
    h1{
        font-size: 1.5em;
    }
}

.RoostNav{
    .roost-title, .account-link{
        color: $navbar-default-link-color;
        a {
            color: $navbar-default-link-color;
        }
    }
    .roost-title{
        position: absolute;
        width: 100%;
        text-align: center;
        z-index: -1;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-right: 60px;
        padding-right: 65px;
        padding-left: 60px;
        &:hover {
            color: $navbar-default-link-color;
        }
    }
}



